import { Typography, Button, Paper, TextField } from "@mui/material";
import { useState } from "react";
import axios from "axios";

import logo from "../../images/logo.png";
import { useDispatch } from "react-redux";
import { updateUser } from "../../redux/api";
import { useNavigate } from "react-router-dom";
import useStyles from "./SingupStyles";

const apiUrl = "https://goodbooks-550.herokuapp.com";
//const apiUrl = "http://localhost:8080";

const Signup = () => {
  const [login, setLogin] = useState(true);
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const classes = useStyles({});
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const handleSubmit = async () => {
    try {
      const response = await axios.post(
        login ? `${apiUrl}/auth/login` : `${apiUrl}/auth/register`,
        {
          username: username,
          password: password,
          confirmPassword: confirmPassword,
          email: email,
        },
        { withCredentials: true }
      );

      // console.log(response.data);
      await updateUser(response.data, dispatch);
      console.log("loged");
      login ? navigate("/") : setLogin(true);
    } catch (error) {
      console.log(error);
    }
  };
  return (
    <div className={classes.container}>
      <Paper elevation={5}>
        <form className={classes.form}>
          <div className={classes.leftSide}>
            <img src={logo} alt="logo" className={classes.logo} />
            <span className={classes.goodbooksName}>GoodBooks</span>
          </div>
          <div className={classes.middleSide}></div>
          <div className={classes.rightSide}>
            <Typography variant="h5">
              {login ? "Log in" : "Register"}
            </Typography>
            <div>
              {login ? (
                <Typography variant="body2" className={classes.routePath}>
                  New to GoodBooks?
                  <span
                    className={classes.formType}
                    onClick={() => setLogin(!login)}
                  >
                    {` Sign up now`}
                  </span>
                </Typography>
              ) : (
                <Typography variant="body2" className={classes.routePath}>
                  Already have an account?
                  <span
                    className={classes.formType}
                    onClick={() => setLogin(!login)}
                  >
                    {` Sign in`}
                  </span>
                </Typography>
              )}
            </div>

            {!login && (
              <>
                <div className={classes.inputField}>
                  <TextField
                    id="standard-basic"
                    label="Username"
                    variant="standard"
                    value={username}
                    onChange={(e) => setUsername(e.target.value)}
                  />
                </div>
              </>
            )}
            <div className={classes.inputField}>
              <TextField
                id="standard-basic"
                label="Email"
                variant="standard"
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
              />
            </div>
            <div className={classes.inputField}>
              <TextField
                id="standard-basic"
                label="Password"
                variant="standard"
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            {!login && (
              <>
                <div className={classes.inputField}>
                  <TextField
                    id="standard-basic"
                    label="Confirm password"
                    type="password"
                    variant="standard"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                  />
                </div>
                <br />
              </>
            )}
            <br />
            <div className={classes.button}>
              <Button variant="contained" onClick={handleSubmit}>
                {login ? "Log in" : "Register"}
              </Button>
            </div>
          </div>
        </form>
      </Paper>
    </div>
  );
};

export default Signup;
